@import "custom-setting.scss";

@each $name, $color in $colorSet {
    .merc-flex-table-#{$name} {
        display: block;

        .merc-flex-table {
            width: 100%;
            box-shadow: map-get($base-table, shadow);
            border-radius: map-get($base-table, radius);
        }

        thead > tr > th {
            background: $color;
            border: none;
            padding: map-get($base-table, padding);

            &:first-child {
                border-top-left-radius: map-get($base-table, radius);
            }

            &:last-child {
                border-top-right-radius: map-get($base-table, radius);
            }
        }

        tbody tr {
            transition: opacity 0.3s, background-color 0.3s;

            td {
                padding: map-get($base-table, padding);
            }

            &:nth-child(even) {
                background-color: map-get($base-table, even-col-color);
            }

            &:hover {
                background-color: map-get($base-table, hover-background-color);
            }
        }

        .no-data {
            &:hover {
                background-color: transparent;
            }

            .no-data-hint {
                min-height: 160px;
                color: #d5d5d5;
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    margin-bottom: 5px;
                }
            }
        }

        @media only screen and (max-width: map-get($base-table, break-point)) {
            .merc-flex-table,
            .merc-flex-table thead,
            .merc-flex-table tbody,
            .merc-flex-table tfoot,
            .merc-flex-table th,
            .merc-flex-table td,
            .merc-flex-table tr {
                display: block;
            }

            .merc-flex-table {
                box-shadow: none;

                thead tr {
                    position: absolute;
                    top: -9999px;
                    left: -9999px;
                }

                tbody tr {
                    border-bottom: 2px solid $color;
                    border-radius: map-get($base-table, radius);
                    box-shadow: map-get($base-table, shadow);

                    &:first-child {
                        border-top: 2px solid $color;
                        border-radius: map-get($base-table, radius);
                    }

                    //  safari 
                    // &:hover {
                        // background-color: transparent;
                    // }

                    td {
                        padding: map-get($base-table, mo-padding) !important;
                        padding-left: 120px !important;
                        height: auto;
                        position: relative;
                        display: flex;
                        align-items: center;

                        &:not(:last-child) {
                            border-bottom: map-get($base-table, mo-border-bottom-width) solid $color;
                        }

                        &::before {
                            content: attr(data-title);
                            position: absolute;
                            top: 0;
                            left: 0;
                            color: $color-dark;
                            font-weight: bold;
                            padding: map-get($base-table, mo-padding);
                        }
                    }

                    &.no-data {
                        td {
                            padding-left: 0 !important;
                            padding: map-get($base-table, mo-padding) !important;
                            justify-content: center;
                        }
                    }
                }
            }
        }
    }
}
