@import "custom-setting.scss";

.merc-slideshow-image {
    .slideshow-images {
        width: 100%;
        max-width: 400px;
        position: relative;

        img {
            width: 100%;
            height: 100%;
            position: absolute;
            object-fit: contain;
            background: map-get($slide-image, image-background-color);
        }

        .slide-image {
            width: 100%;
            height: 0px;
            padding-bottom: 100%;
            position: relative;
        }

        .active {
            border: map-get($slide-image, focus-border);
        }

        .more-images-mask {
            background-color: rgba(0, 0, 0, 0.5);
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 10;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
        }

        .no-data {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            background: rgba(0, 0, 0, 0.8);
            color: white;
            z-index: 12;
            font-size: 20px;
        }
    }
}

.cdk-overlay-pane .merc-slideshow-image .overlay {
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 11;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.6);
    overflow-x: hidden;
    transition: 0.5s;

    .overlay-close-btn {
        position: absolute;
        top: 20px;
        right: 20px;
        img {
            width: 25px;
        }
    }

    .overlay-slideshow-images {
        width: 100%;
        max-width: 600px;

        .slide-image {
            width: 100%;
            height: 0px;
            padding-bottom: 100%;
            position: relative;

            img {
                width: 100%;
                height: 100%;
                position: absolute;
                object-fit: contain;
                background: map-get($slide-image, image-background-color);
            }
        }

        .active {
            border: map-get($slide-image, focus-border);
        }

        @media (max-width: 767px) {
            max-width: 300px;
        }
    }

    .slide-arrow {
        margin: 0 10px;
        opacity: 0;
        cursor: initial;
        pointer-events: none;
    }

    .visiable {
        opacity: 1;
        cursor: pointer;
        pointer-events: visible;
    }

    .page-count {
        text-align: right;
        color: $color-white;
    }

    .overlay-preview-item {
        width: 60px;
        margin: 0 3px;
    }
}
