/* scss custom-setting custom-setting  */
/**  */
$color-white: #fff !default;
$color-dark: rgba(0, 0, 0, 0.8) !default;
// 
$color-light-gray: #e5e5e5 !default;

// 
$primary-color: #b9e1e4 !default;
// 
$primary-variant-color: #54be9f !default;
// 
$secondary-color: #4b8ac5 !default;
// 
$secondary-variant-color: #e6f3f7 !default;
// 
$third-color: #800080 !default;
// 
$third-variant-color: #eed7ed !default;
// danger
$danger-color: #E73D4A !default;

// 
$colorSet: "primary" $primary-color, "primary-variant" $primary-variant-color, "secondary" $secondary-color,
  "secondary-variant" $secondary-variant-color, "third" $third-color, "third-variant" $third-variant-color;

/**  */
$font-sz: (
  xl: 30px,
  lg: 18px,
  md: 16px,
  sm: 14px,
  xs: 12px,
) !default;

$font-weight: (
  fatty: 600,
  normal: normal,
) !default;

/**  */
/* base btn start */
$btn: (
  border-radius: 7px,
  shadow: 1px 1px 2px 0 rgba(21, 28, 42, 0.17),
) !default;
$btn-height: (
  lg: 40px,
  md: 35px,
  sm: 30px,
) !default;
$btn-width: (
  xl: 200px,
  lg: 150px,
  md: 120px,
  sm: 90px,
  xs: 60px,
) !default;
/* base btn end */

/** base card start*/
$base-card: (
  background-color: $color-white,
  box-shadow: none,
  padding: 0,
  border: 0,
  border-radius: 6px,
) !default;

$base-card-header: (
  align-self: center,
  font-size: map-get($font-sz, lg),
  font-weight: map-get($font-weight, fatty),
  padding: 0 15px,
) !default;

$base-card-content: (
  padding: 10px 15px,
) !default;
/** base card end*/

/* base table start */
$base-table: (
  // hover 
  hover-background-color: darken($color-light-gray, 15%),
  // 
  even-col-color: $color-light-gray,
  // 
  radius: 7px,
  // 
  shadow: 1px 2px 4px #d5d5d5,
  // thtd 
  padding: 4px 8px,
  // thtd flex table 
  mo-padding: 10px 8px,
  // pxflex table 
  break-point: 991px,
  // border-bottomflex table 
  mo-border-bottom-width: 1px
) !default;

/* base tab start */
$base-tab: (
  // (merc-tab  mercType )
  tab-color: $primary-color,
  // tab 
  tab-border-width: 1px,
  // content 
  content-border-width: 10px,
  // 
  radius: 7px,
  // 
  font-size: map-get($font-sz, lg)
) !default;
/* base tab end */

/* slide image start */
$slide-image: (
  image-background-color: $color-light-gray,
  focus-border: 2px solid $primary-color
) !default;
/* slide image end */