  top: 115%;
  border: 1px solid #efefef;
  border-radius: 3px;
  max-width: 200px;
  background-color: #fff;
  z-index: 999;
}
.cdk-overlay-container .dates.active {
  display: block;
}
.cdk-overlay-container .header-box {
  display: flex;
  justify-content: space-around;
  padding: 10px 2px 0;
}
.cdk-overlay-container .header-label {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cdk-overlay-container .arrow-btn {
  cursor: pointer;
  width: 16px;
  text-align: center;
  color: #008f6a;
  display: inline-block;
}
.cdk-overlay-container .arrow-btn:hover {
  color: #000;
}
.cdk-overlay-container .weeks,
.cdk-overlay-container .days {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
}
.cdk-overlay-container .weeks {
  color: #008f6a;
}
.cdk-overlay-container .week,
.cdk-overlay-container .day {
  display: flex;
  justify-content: center;
  align-items: center;
}
.cdk-overlay-container .day,
.cdk-overlay-container .year {
  cursor: pointer;
}
.cdk-overlay-container .years,
.cdk-overlay-container .months {
  display: flex;
  justify-content: center;
  align-items: center;
}
.cdk-overlay-container .not-present {
  cursor: not-allowed;
  color: #efefef;
}
.cdk-overlay-container .day.selected {
  background-color: #008f6a;
  color: #fff;
  width: 28px;
  height: 28px;
  border-radius: 50%;
}
.cdk-overlay-container .day.selected.not-present {
  background-color: #efefef;
}
.cdk-overlay-container .today {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #008f6a;
  color: #008f6a;
  cursor: pointer;
}
.cdk-overlay-container .today.not-present {
  color: #efefef;
  cursor: not-allowed;
}
.cdk-overlay-container .hr {
  color: #efefef;
  margin: 10px;
  opacity: 0.8;
}

