/**  */
$color-white: #fff;
$color-dark: rgba(0, 0, 0, 0.8);
// 
$primary-color: #008f6a;
// 
$primary-variant-color: #213938;
// 
$secondary-color: #3e6b69;
// 
$secondary-variant-color: #f8c73f;
// 
$third-color: #58a8dd;
// 
$third-variant-color: #fffaf1;

// 
$color-light-gray: #efefef;
// 
$color-dark-gray: #333333;
// 
$color-white: #fff;
$color-lighter-gray: #f9f9f9;
$color-silver: #d5d5d5;
$color-green: #87b848;
$danger-color: #bf4e58;

$colorSet: "primary" $primary-color, "primary-variant" $primary-variant-color, "secondary" $secondary-color,
  "secondary-variant" $secondary-variant-color, "third" $third-color, "third-variant" $third-variant-color,
  "danger" $danger-color;

/**  */
$font-sz: (
  xl: 30px,
  lg: 18px,
  md: 16px,
  sm: 14px,
  xs: 12px,
);

$font-weight: (
  fatty: 600,
  normal: normal,
);

/**  */
/* base btn start */
$btn: (
  border-radius: 3px,
  shadow: 1px 1px 2px 0 rgba(21, 28, 42, 0.17),
);
$btn-height: (
  lg: 40px,
  md: 35px,
  sm: 30px,
);
$btn-width: (
  xl: 200px,
  lg: 150px,
  md: 120px,
  sm: 90px,
  xs: 70px,
);
/* base btn end */

/** base card start*/
$base-card: (
  background-color: $color-white,
  box-shadow: none,
  padding: 0,
  border: $color-silver 1px solid,
  border-radius: 6px,
);

$base-card-header: (
  align-self: center,
  font-size: map-get($font-sz, lg),
  font-weight: map-get($font-weight, fatty),
  padding: 0 15px,
);

$base-card-content: (
  padding: 10px 15px,
);
/** base card end*/

/* base table start */
$base-table: (
  // hover 
  hover-background-color: darken($color-light-gray, 15%),
  // 
  even-col-color: $third-variant-color,
  // 
  radius: 7px,
  // 
  shadow: 1px 2px 4px #d5d5d5,
  // thtd 
  padding: 8px 8px,
  // thtd flex table 
  mo-padding: 3px 8px,
  // pxflex table 
  break-point: 767px,
  // border-bottomflex table 
  mo-border-bottom-width: 0px
);

/* base tab start */
$base-tab: (
  // (merc-tab  mercType )
  tab-color: $primary-color,
  // tab 
  tab-border-width: 1px,
  // content 
  content-border-width: 1px,
  // 
  radius: 7px,
  // 
  font-size: map-get($font-sz, lg)
);
/* base tab end */
@import "../node_modules/merc-lib/src/styles/merc-lib.scss";
